
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.modal {
  padding: 15px;
}

.header {
  margin-bottom: 15px;
}

.support {
    position: relative;
    top: 2px;
    left: 1px;
}

.status {
    font-size: 14px;
    word-break: break-word;
}

.status,
.type {
    word-break: break-word;
}

.info {
  margin-top: 15px;

  li:not(:last-child) {
    margin-bottom: 10px;
  }

  strong:first-child {
